import React, { Component } from 'react';
import Particles from 'react-particles-js';
import AOS from 'aos';

import { ComponentLeader, Loader } from '../widgets';
import { YeahModal,SalesModal } from '../widgets/modals';

const features = require('./data').default;
require('../../styles/aos/aos.scss');

class ShortTermIndexPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      showModal: false
    }
    AOS.init();
  }

  componentDidMount() {
    (function() {

      let items = document.querySelectorAll(".timeline li");

      function isElementInView(el){
        let rect = el.getBoundingClientRect();
        return(
        rect.top >= 0&&
        rect.left >= 0 &&
        rect.bottom <=(window.innerHeight||document.documentElement.clientHeight)&&
        rect.right <=(window.innerWidth||document.documentElement.clientWidth)
        );
      }

      function callbackFunc(){
        for(let i=0;i<items.length;i++)
        {
          if (isElementInView(items[i])){
            items[i].classList.add("in-view");
          }
        }
      }

      window.addEventListener("load",callbackFunc);
      window.addEventListener("resize",callbackFunc);
      window.addEventListener("scroll",callbackFunc);

})();
  }

  openSalesModal() {
    this.setState({
      showSalesModal: true
    });
  }


  renderTimeline() {
    return (
      <div className="timeline">
        <ul>
          <h1>Service Process</h1>
          <li><div>
            <h2>August</h2>
            <h3>第一次会面</h3>
            <h5>· 填寫個人背景故事</h5>
            <h5>· 挑選PS主題</h5>
          </div></li>
          <li><div>
            <h2>September</h2>
            <h3>完成PS初稿</h3>
            <h5>· 討論初稿故事細節</h5>
            <h5>· 潤色PS文字表達</h5>
            <h5>· 深入思考PS主旨</h5>            
          </div></li>

          <li><div>
            <h2>10月份</h2>
            <h3>完成PS第二稿</h3>
            <h5>· 潤色PS文字表達</h5>
            <h5>· 歐耶總監審閱文章</h5>
          </div></li>	

          <li><div>
            <h2>11月份</h2>
            <h3>完成PS最終稿</h3>
            <h5>· 填寫申請表格</h5>
            <h5>· 参加申請講座</h5>
          </div></li>

          <li><div>
            <h1>成功转学</h1>
          </div></li>	
          <br />
        </ul>
      </div>
    );
  }

  renderFeatures() {
    if (features) {
      return features.map((feature, index) => {
        return (
          <div className="col-xs-6 col-md-4" key={index} data-aos="fade-up" id="feature-on">
            <div id="image">
              <img alt="" src={feature.link}/>                                    
            </div>
            <span>
              <h3>{feature.title}</h3>
              <p>{feature.description}</p>
            </span>
          </div>
        );
      });
    } 
    return <Loader />
  }

  renderTable() {
    return (
      <div className="table container">
        <div className="row">
          <div className="col-3">
            <div className="table-cell" />
            <span>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> Target School</span> 
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 背景了解规划</span>
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 深度素材挖掘</span>
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 文书提升润色</span>
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 1对1视频探讨</span>
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 申请表格指导</span>
              </div>
              <div className="table-cell">
                <span><br className="hidden-xs-down" /> 总监私人服务</span> 
              </div>
            </span>                        
          </div>
          <div className="col-3">
            <div className="table-cell service-name-1">臻品 经济班机</div>
            <span>
              <div className="table-cell">
                    普通UC
              </div>
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell unchecked" />
              <div className="table-cell unchecked" />
              <div className="table-cell unchecked" />
            </span>                        
          </div>
          <div className="col-3">
            <div className="table-cell service-name-2">尊享 商务班机</div>
            <span>
              <div className="table-cell">
                  UCB/UCLA
              </div>
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell unchecked" />
            </span>                        
          </div>
          <div className="col-3">
            <div className="table-cell service-name-3">钻石 头等班机</div>
            <span>
              <div className="table-cell">
                  UCB/私立大学
              </div>
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
              <div className="table-cell checked" />
            </span>                        
          </div>
        </div>
      </div>
    );
  }

  renderEnrollNow() {
    return (
      <div className="take_actions">
        <Particles/>
        <div className="card">
          <div className="title">
              實現你的夢想
          </div>
          <div className="action_button" onClick={this.openSalesModal.bind(this)}>
              聯繫我們
          </div>
          <div className="description">
              夢想永不止步
          </div>
        </div>
      </div>
    )
  }

  render() {
    return (
      <div>
        <ComponentLeader title="Short-Term Service"/>
        <div className="short-term">
          <div className="container-fluid">
            {this.renderTimeline()}
            <div className="features">
              <div className="row">
                {this.renderFeatures()}                      
              </div>
            </div>
            {this.renderTable()}
            {this.renderEnrollNow()}
            {this.state.showSalesModal && <YeahModal><SalesModal /></YeahModal>}
          </div>
        </div>
      </div>
    );
  }
}

export default ShortTermIndexPage;



